import React, { FC } from 'react'
import clsx from 'clsx'
import { Condition } from '../Condition'
import { Typography } from '../Typography'
import { Button } from '../../buttons/Button'
import { CloseCrossIcon } from '../../icons/CloseCross'
import { InformationProps } from './types'
import './Information.scss'

const Information: FC<InformationProps> = ({
  description = '',
  isMobile = false,
  onDelete,
  type = '',
  typeName = '',
  children,
  status,
  withLink,
}) => {
  const showDescription = (): JSX.Element => {
    if (withLink) {
      return withLink
    }
    return (
      <Typography
        className='Information__Description'
        text={description}
        type='Body2'
      />
    )
  }
  const isStatusNew = status === 'new'
  return (
    <div
      className={clsx('Information', {
        [`--${type}`]: isStatusNew,
      })}
    >
      <div className='Information__TopBar'>
        <Typography
          className={clsx('Information__Title', {
            '--new': isStatusNew,
          })}
          text={typeName}
          type='Body1B'
        />
        <Condition match={!isMobile && Boolean(onDelete)}>
          <Button
            className='Information__DeleteButton'
            onClick={(): void => onDelete()}
            size='S'
            text='Удалить'
          />
        </Condition>
        <Condition match={isMobile && Boolean(onDelete)}>
          <CloseCrossIcon
            className='Information__CloseButton'
            height={20}
            onClick={(): void => onDelete()}
            type='light'
            width={20}
          />
        </Condition>
      </div>
      {showDescription()}
      {children}
    </div>
  )
}

export default Information
